<template>
  <div>
    <el-dialog
      title="处理"
      :visible.sync="wareHouseFormVisible"
    >
      <span
        v-if="isDelete"
        class="delete-contract"
        @click="deleteRecord('合同')"
      >删除该合同？</span>
      <span class="dialog-title">平仓信息</span>
      <el-form
        :model="formData"
        :inline="true"
        ref="formData"
      >
        <el-form-item
          label="仓单编号"
          :label-width="formLabelWidth"
          style="width:365px;"
        >
          {{form.wareHouseId}}
        </el-form-item>
        <el-form-item
          label="交易帐户"
          :label-width="formLabelWidth"
        >
          {{form.customerId}}
        </el-form-item>
      </el-form>
      <el-form
        :model="formData"
        :inline="true"
        :rules="rules"
        ref="formData"
      >
        <el-form-item
          label="交易方式"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="txType"
        >
          <el-select
            v-model="formData.txType"
            placeholder="请选择交易方式"
            :disabled="liquidated"
          >
            <el-option
              label="回购"
              value="1"
            ></el-option>
            <el-option
              label="抛售"
              value="2"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="平仓总价"
          :label-width="formLabelWidth"
          prop="liquidateValue"
        >
          <el-input
            v-model="formData.liquidateValue"
            :disabled="liquidated"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="平仓时间"
          :label-width="formLabelWidth"
          style="width:365px;"
          prop="time"
        >
          <el-date-picker
            type="datetime"
            placeholder="选择日期时间"
            v-model="formData.time"
            style="width: 100%;"
            :disabled="liquidated"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label="平仓时间期货价格"
          :label-width="formLabelWidth"
          prop="liquidatePrice"
        >
          <el-input
            v-model="formData.liquidatePrice"
            :disabled="liquidated"
          ></el-input>
        </el-form-item>
      </el-form>
      <div class="buttonGroup">
        <el-button
          type="primary"
          @click="liquidate('formData')"
          v-if="!liquidated"
        >确认平仓</el-button>
        <el-button
          type="primary"
          @click="liquidate('formData')"
          v-else
        >确认还款并归档</el-button>
      </div>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="wareHouseFormVisible = false">取 消</el-button>
        <!-- <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button> -->
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { rules } from "../validate.js";
export default {
  props: ["dialogFormVisible", "form", "isDelete"],
  data() {
    return {
      formData: {
        wareHouseId: "", //仓单编号
        customerId: "", //交易帐户
        time: "", //平仓时间
        txType: "", //交易方式
        liquidatePrice: "", //平仓时间期货价格
        liquidateValue: "" //平仓总价
      },
      wareHouseFormVisible: this.dialogFormVisible,
      formLabelWidth: "135px",
      rules: rules,

      liquidated: false //判断是否平仓
    };
  },
  watch: {
    dialogFormVisible(val) {
      this.wareHouseFormVisible = val; //新增result的watch，监听变更并同步到myResult上
    },
    wareHouseFormVisible(val) {
      this.$emit("dialogFormVisibleChange", val); //③组件内对dialogFormVisibleChange变更后向外部发送事件通知
    }
  },
  methods: {
    liquidate(formData) {
      this.$refs[formData].validate(valid => {
        if (valid) {
          this.liquidated = true;
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    deleteRecord(type){
      this.$emit("deleteRecord", type);
    }
  },
  components: {}
};
</script>

<style>
.delete-contract {
  display: block;
  margin-bottom: 15px;
  width: 85px;
}
.delete-contract:hover {
  cursor: pointer;
  border-bottom: 1px solid black;
}
</style>



