const rules = {
  txType: [{
    required: true,
    message: '请输入交易方式',
    trigger: 'change'
  }],
  time: [{
    type: 'date',
    required: true,
    message: '请输入时间',
    trigger: 'change'
  }],
  liquidateValue: [{
    required: true,
    message: '请输入平仓总价',
    trigger: 'blur'
  }],
  liquidatePrice: [{
    required: true,
    message: '请输入平仓时间期货价格',
    trigger: 'blur'
  }],
  username: [{
    required: true,
    message: "请输入用户名",
    trigger: "blur"
  }],
  password: [{
    required: true,
    message: "请输入密码",
    trigger: "blur"
  }]
}

export {
  rules
}



