<template>
  <div>
    <vSearch
      @getOption="getOption"
      @searchInfo="searchInfo"
      @clearSearchInfo="clearSearchInfo"
      :pageType="pageType"
    ></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      :default-sort="{prop: 'date', order: 'descending'}"
    >
      <el-table-column
        prop="date"
        label="操作"
        min-width="45%"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="operateRow(scope.row)"
            type="text"
            size="small"
          >
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        sortable
        :formatter="statusSwitch"
        min-width="65%"
      >
      </el-table-column>
      <el-table-column
        prop="customerId"
        label="客户号"
        min-width="65%"
      >
      </el-table-column>
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="processName"
        label="流程名称"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="contractNum"
        label="合同编号"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="wareHouseId"
        label="仓单编号(仓单总数)"
        min-width="100%"
      >
      </el-table-column>
      <el-table-column
        prop="goodsType"
        label="仓单品种"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="price"
        label="仓单交易价格"
        sortable
        min-width="100%"
      >
      </el-table-column>
      <el-table-column
        prop="duration"
        label="期限"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="txTime"
        label="交易时间"
        sortable
      >
      </el-table-column>
      <el-table-column
        prop="companyNmae"
        label="发起机构"
        sortable
      >
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage"
      >
      </el-pagination>
    </div>
    <handleDialog
      :dialogFormVisible="dialogFormVisible"
      :form="form"
      :isDelete="false"
      @dialogFormVisibleChange="dialogFormVisibleChange"
    ></handleDialog>
  </div>
</template>

<script>
import vSearch from "../components/Search.vue";
import handleDialog from "../components/HandleDialog.vue";

export default {
  data() {
    return {
      pageType: 0,
      searchForm: {
        option: "",
        inputText: ""
      },
      tableData: [
        {
          status: "1",
          customerId: "hz-hzjylxmyyxgs-180926",
          customerName: "杭州久益隆鑫贸易有限公司",
          processName: "仓单处理",
          wareHouseId: "al0260025533~al0260025548",
          contractNum: "NHZB-CDZY-B-AL-180001",
          goodsType: "铝",
          price: "6,596,691.4",
          duration: "30",
          txTime: "2018-09-21",
          companyNmae: "浙江南华资本管理有限公司"
        }
      ],
      currentPage: 1,
      pageSize: 1,
      totalPage: 100,
      form: {
        wareHouseId: "", //仓单编号
        txAccount: "", //交易帐户
        time: "", //平仓时间
        txType: "", //交易方式
        liquidatePrice: "", //平仓时间期货价格
        liquidateValue: "" //平仓总价
      },
      dialogFormVisible: false
    };
  },
  mounted() {},
  methods: {
    getOption(value, inputText) {
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo() {
      console.log(this.searchForm.option + "+" + this.searchForm.inputText);
    },
    clearSearchInfo() {
      this.searchForm.option = "";
      this.searchForm.inputText = "";
    },
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    operateRow(row) {
      this.form = row;
      this.dialogFormVisible = true;
    },
    dialogFormVisibleChange(val){
      this.dialogFormVisible = val;
    },
    //操作状态处理
    statusSwitch(row, column) {
      switch (row.status) {
        case "0":
          return "未处理";
          break;

        case "1":
          return "处理中";
          break;
      }
    }
  },
  components: {
    vSearch,
    handleDialog
  }
};
</script>

<style>
.pagination {
  text-align: center;
}
.dialog-title {
  font-size: 20px;
  margin-bottom: 20px;
  display: block;
}
.buttonGroup {
  text-align: center;
  margin-left: -50px;
}
</style>



